import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { ko } from "date-fns/esm/locale";
import { addDays, differenceInDays } from "date-fns";
import "../CheckInfo.css";
import "../Calendar.css";
import axios from "axios";

function CheckInfo() {
  const navigate = useNavigate();
  const today = new Date();

  //   
  const [selectedData, setSelectedData] = useState({
    startDate: null,
    endDate: null,
    times: { start: "", end: "18-20" },
    price: "5",
    themes: [],
    with: null,
    location: null,
  });

  const handleDateChange = (dates) => {
    const [start, end] = dates;
    const newEndDate =
      end && differenceInDays(end, start) > 2 ? addDays(start, 2) : end;

    setSelectedData({
      ...selectedData,
      startDate: start ? start : null,
      endDate: newEndDate ? newEndDate : null,
    });
  };

  const handleSelect = (name, value) => {
    setSelectedData({
      ...selectedData,
      [name]: value,
    });
  };

  const handleTimeSelect = (e) => {
    handleSelect("times", {
      ...selectedData.times,
      [e.target.name]: e.target.value,
    });
  };

  const handlePriceSelect = (e) => {
    handleSelect("price", e.target.value);
  };

  const handleLocationSelect = (index) => {
    handleSelect("location", index === selectedData.location ? null : index);
  };

  const handleWithSelect = (index) => {
    handleSelect("with", index === selectedData.with ? null : index);
  };

  const handleThemeSelect = (theme) => {
    if (selectedData.themes.includes(theme)) {
      if (selectedData.themes.length >= 1) {
        setSelectedData({
          ...selectedData,
          themes: selectedData.themes.filter((t) => t !== theme),
        });
      }
    } else if (selectedData.themes.length < 3) {
      setSelectedData({
        ...selectedData,
        themes: [...selectedData.themes, theme],
      });
    } else {
      alert(" 3  !");
    }
  };

  const startDateString = selectedData.startDate
    ? selectedData.startDate.toLocaleDateString("ko-KR", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
      })
    : "";

  const endDateString = selectedData.endDate
    ? selectedData.endDate.toLocaleDateString("ko-KR", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
      })
    : "";

  const duration =
    differenceInDays(selectedData.endDate, selectedData.startDate) + 1;

  const finalData = {
    ...selectedData,
    startDate: startDateString,
    endDate: endDateString,
  };

  //   Json  
  const jsonData = JSON.stringify({
    ...selectedData,
    startDate: startDateString,
    endDate: endDateString,
  });

  const checkValidity = async () => {
    if (
      selectedData.startDate === null ||
      selectedData.times.start === null ||
      selectedData.times.end === null ||
      selectedData.price === null ||
      selectedData.themes.length === 0
    ) {
      const emptyData = [];

      if (selectedData.startDate === null || selectedData.endDate === null) {
        emptyData.push("");
      }
      if (selectedData.times.start === null) {
        emptyData.push("");
      }
      if (selectedData.times.end === null) {
        emptyData.push("");
      }
      if (selectedData.price === null) {
        emptyData.push(" ");
      }
      if (selectedData.themes.length === 0) {
        emptyData.push("");
      }
      alert(`' ${emptyData.join(", ")} '() !`);
    } else {
      try {
        // POST  
        const response = await axios.post(
          "http://3.37.27.117:8000/plan/",
          jsonData
        );

        // POST   response -> (( * 3) * 3) kakaoId
        navigate("/options", {
          state: {
            duration: duration,
            finalData: finalData,
            responseData: response.data,
          },
        });
      } catch (error) {
        console.error(error);
      }
    }
  };

  // //     ! // //
  console.log(jsonData);
  // // // // // // // // // //

  return (
    <div className="CheckInfo">
      <div className="about">
        <button id="back" onClick={() => navigate(-1)}>
          {`<`}
        </button>
        <text>   ... {"\uD83C\uDF4A"}</text>
      </div>
      <div className="Q">
        <text>1.  .</text>
        <DatePicker
          locale={ko}
          dateFormat="yyyy.MM.dd"
          minDate={today}
          selected={selectedData.startDate}
          onChange={handleDateChange}
          startDate={selectedData.startDate}
          endDate={selectedData.endDate}
          selectsRange
          placeholderText=" 2 3  "
        />
      </div>
      <div className="Q">
        <text>2.     .</text>
        <div className="CheckTime">
          <text></text>
          <select
            name="start"
            value={selectedData.times.start}
            onChange={handleTimeSelect}
          >
            <option value={""}> (8-9)</option>
            <option value={""}> (12-13)</option>
            <option value={""}> (14-15)</option>
          </select>
          <text>|</text>
          <text></text>
          <select
            name="end"
            value={selectedData.times.end}
            onChange={handleTimeSelect}
          >
            <option value={"18-20"}>18-20</option>
            <option value={"20-21"}>20-21</option>
            <option value={"21-22"}>21-22</option>
          </select>
        </div>
      </div>
      <div className="Q">
        <text>3.  1  .</text>
        <span className="price">
          <select value={selectedData.price} onChange={handlePriceSelect}>
            <option value={"5"}>5</option>
            <option value={"10"}>10</option>
            <option value={"15"}>15</option>
            <option value={"20"}>20</option>
          </select>
        </span>
      </div>
      <div className="Q">
        <text>4.   . ( 3  )</text>
        <span className="theme">
          <button
            className={
              selectedData.themes.includes("") ? "selected" : ""
            }
            onClick={() => handleThemeSelect("")}
          >
            <span className="button-text"></span>
          </button>
          <button
            className={selectedData.themes.includes("") ? "selected" : ""}
            onClick={() => handleThemeSelect("")}
          >
            <span className="button-text"></span>
          </button>
          <button
            className={selectedData.themes.includes("") ? "selected" : ""}
            onClick={() => handleThemeSelect("")}
          >
            <span className="button-text"></span>
          </button>
          <br />
          <button
            className={
              selectedData.themes.includes("") ? "selected" : ""
            }
            onClick={() => handleThemeSelect("")}
          >
            <span className="button-text"></span>
          </button>
          <button
            className={selectedData.themes.includes("") ? "selected" : ""}
            onClick={() => handleThemeSelect("")}
          >
            <span className="button-text"></span>
          </button>
          <button
            className={selectedData.themes.includes("") ? "selected" : ""}
            onClick={() => handleThemeSelect("")}
          >
            <span className="button-text"></span>
          </button>
        </span>
      </div>
      <div className="Q">
        <text>5.   . ( X)</text>
        <span className="with">
          <button
            className={selectedData.with === "" ? "selected" : ""}
            onClick={() => handleWithSelect("")}
          >
            
          </button>
          <button
            className={selectedData.with === "" ? "selected" : ""}
            onClick={() => handleWithSelect("")}
          >
             
          </button>
          <button
            className={selectedData.with === "" ? "selected" : ""}
            onClick={() => handleWithSelect("")}
          >
             
          </button>
          <button
            className={selectedData.with === "" ? "selected" : ""}
            onClick={() => handleWithSelect("")}
          >
             
          </button>
        </span>
      </div>
      <div className="Q">
        <text>6.    . ( X)</text>
        <span className="location">
          <button
            className={selectedData.location === "" ? "selected" : ""}
            onClick={() => handleLocationSelect("")}
          >
             
          </button>
          <button
            className={selectedData.location === "" ? "selected" : ""}
            onClick={() => handleLocationSelect("")}
          >
             
          </button>
        </span>
      </div>
      <button id="result" onClick={checkValidity}>
          
      </button>
    </div>
  );
}

export default CheckInfo;
